.switch{position:relative;display:inline-block;width:42px;height:24px}.switch input{opacity:0;width:0;height:0}.switch .slider{position:absolute;cursor:pointer;top:0;left:0;right:0;bottom:0;background-color:#ccc;-webkit-transition:.4s;transition:.4s}.switch .slider:before{position:absolute;content:"";height:14px;width:14px;left:4px;bottom:6px;background-color:white;-webkit-transition:.4s;transition:.4s;box-shadow:-1px 2px 3px 0 rgba(34,41,47,0.4)}.switch input:disabled+.slider{background-color:#efefef !important}.switch input:checked:disabled+.slider{background-color:rgba(115,103,240,0.65) !important}.switch input:checked+.slider{background-color:#7367f0}.switch input:focus+.slider{box-shadow:0 0 1px #7367f0}.switch input:checked+.slider:before{-webkit-transform:translateX(20px);-ms-transform:translateX(20px);transform:translateX(20px)}.switch .slider.round{border-radius:34px}.switch .slider.round:before{border-radius:50%}
